.header-container {
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  display: flex;
  z-index: 3;
  background: rgb(251, 251, 251);
  filter: drop-shadow(0 0 2px gray);
  padding-top: 0.5rem;
}

.header-container a {
  text-decoration: none;
  color: rgb(86, 79, 79);
  font-size: 0.8rem;
  font-weight: 700;
  cursor: pointer;
}
.header-container a:hover {
  color: #05bf6b;
}

.header-left {
  flex: 1;
  align-items: center;
  letter-spacing: 0.1rem;
  gap: 1rem;
  margin-left: 3rem;
  display: flex;
}

.header-logo {
  width: 55px;
  height: 55px;
}

.header-right {
  flex: 1;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  margin-right: 2rem;
  display: flex;
}

.header-right a {
  font-size: 0.9rem !important;
}

.stores {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.location-icon {
  width: 1.4rem;
}

.green-link-button {
  background-color: #008248;
  color: white !important;
  padding: 9px 15px;
  border-radius: 50px;
  transition: background-color 0.25s ease-out;
}
.green-link-button:hover {
  background-color: #05bf6b;
}

.login {
  color: black !important;
  padding: 9px 16px;
  border-radius: 50px;
  border: 1px solid black;
  margin-left: 1rem;
  transition: background-color 0.25s ease-out;
}
.login:hover {
  background-color: rgb(231, 231, 231);
}

.join {
  background-color: black;
  color: white !important;
  padding: 9px 16px;
  border-radius: 50px;
  transition: background-color 0.25s ease-out;
}
.join:hover {
  background-color: rgb(74, 74, 74);
}
