.home-container {
  display: flex;
  margin-top: 5.5rem;
  background-color: #f7f7f7;
}

.home-page-left {
  position: fixed;
}

.home-page-video {
  margin-top: -80px;
  min-height: 100vh;
  max-width: 40vw;
  background-color: rgb(214, 239, 223);
}

.home-page-left-text {
  position: absolute;
  bottom: 4.5rem;
  left: 2.5rem;
  z-index: 9;
  font-size: 2rem;
}

.home-page-right {
  margin-left: 40vw;
  display: flex;
  flex-direction: column;
  padding: 1.5rem 2rem 1rem 2.5rem;
}

.iced-shaken-espresso {
  width: 100%;
}

.yulafli {
  padding: 2.5rem 4rem 2rem 2.25rem;
}

.yulafli-header {
  font-weight: 700;
  font-size: 1.2rem;
  margin: 0;
}

.yulafli-text {
  line-height: 1.3rem;
  margin: 1.25rem 0;
}

.yulafli a {
  text-decoration: none;
  color: rgb(86, 79, 79);
  font-size: 0.8rem;
  font-weight: 700;
  cursor: pointer;
}

.green-link-button {
  background-color: #008248;
  color: white !important;
  padding: 9px 15px;
  border-radius: 50px;
  transition: background-color 0.25s ease-out;
}
.green-link-button:hover {
  background-color: #05bf6b;
}

.getiryemek {
  background-color: white !important;
  color: #008248 !important;
  border: 1px solid #05bf6b;
  margin-left: 1rem;
}
.getiryemek:hover {
  background-color: rgb(231, 231, 231) !important;
}

.home-right-box-container {
  display: flex;
  flex-direction: column;
  gap: 3.5rem;
}

.home-right-box {
  display: flex;
}

.home-right-box-image {
  width: 50%;
  margin: auto;
  margin-top: 0;
}

.home-right-box-right-div {
  margin-left: 1.3rem;
}

.home-right-box-right-div a {
  text-decoration: none;
  color: rgb(86, 79, 79);
  font-size: 0.8rem;
  font-weight: 700;
  cursor: pointer;
}

.home-right-box-header {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
  margin: 0;
}

.home-right-box-text {
  line-height: 1.3rem;
}

.openable-bars {
  font-size: 1.5rem;
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  gap: 4.25rem;
}

.bar-list-item {
  padding: 0 1rem;
  cursor: pointer;
}

.openable-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-size: 1rem;
  font-weight: 600;
  padding: 1rem;
  margin: 0;
  margin-top: -3.5rem;
}

.openable-list a {
  text-decoration: none;
  color: #6c6c6c;
}

.clickable-button {
  float: right;
  transform: rotate(90deg);
  cursor: pointer;
  border: none;
  background-color: inherit;
  transition: color 0.25s ease-out;
  border-radius: 50%;
  height: 2.5rem;
  width: 2.5rem;
}
.clickable-button:hover {
  background-color: rgb(237, 234, 234);
}

.home-starbucks-delivers-button {
  text-decoration: none;
  color: inherit;
}

.footer-links {
  border-top: 1px solid gray;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
  margin-top: 2rem;
  margin-left: 1rem;
}

.footer-links a {
  text-decoration: none;
  color: inherit;
  font-weight: 700;
}

.social-links {
  display: flex;
  gap: 1rem;
  margin: 1rem 0;
}

.language {
  width: fit-content;
  background-color: white !important;
  color: #008248 !important;
  border: 1px solid #05bf6b;
  font-weight: 700 !important;
  padding: 7px 14px;
  margin-bottom: 4rem;
}
